body {
    background-color: rgb(19, 19, 19);
    color: rgb(231, 231, 231);
}


.header {
    text-align: center;
 }
 
 
 .logo {
    width: 600px;
    height: 200px;
 }
 
 
 .body {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
 }
 
 
 .button {
    color: white;
    background-color: transparent;
    border: white 1px solid;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    font-family: 'Times New Roman',;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0,2), 0 6px 20px 0 rgba(0, 0, 0, 0, 19);
    border-radius: 8px;
 }
 
 .button:hover {
    background-color: white;
    color:rgb(19, 19, 19);
 }
 
 #champions {
   display: grid;
   grid-template-columns: repeat(5, 1fr);
   grid-gap: 10px, 10px;
   width: 100%;
 }
 
 
.card {
   text-align: center;
   border-radius: 5px;
   background: transparent;
   font-family:'Impact','Arial Narrow Bold';
   margin: 10px;
 }
 
 
.types {   
    border: 1px solid rgb(255, 255, 255);
    font-weight: bold;
    background: transparent
}

.title{
   font-family: 'Franklin Gothic Medium', cursive;
   font-weight: bolder;
}

.version {
   font-size: 0.8rem;
   color: rgb(255, 255, 255);
}
.types:hover{
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
}
 
.card img {
   border: 1px solid rgb(255, 255, 255);
   filter: opacity(0.1) drop-shadow(0 0 0 rgb(218, 190, 101));
   filter:grayscale(1);
}

.card img:hover{
   filter: opacity(1) drop-shadow(0 0 0 rgb(218, 190, 101));
}

.card .info {
    display: none;
}

.card:hover .info{
    display: block;
}



@keyframes animation1 {
   0%{
      filter: grayscale(1);
   }
}